module.exports = "bootstrapUtils.addStyle(Button, 'custom');\n\nconst customButtonStyle = (\n  <div>\n    <style type=\"text/css\">\n      {`\n    .btn-custom {\n        background-color: purple;\n        color: white;\n    }\n    `}\n    </style>\n    <Button bsStyle=\"custom\">Custom</Button>\n  </div>\n);\n\nrender(customButtonStyle);\n"


